<template lang="">
	<div :class="['color-item', {'color-item--locked':locked}]">
		<h2 @click="copyToClickboard($event)"></h2>
		<button ref="btnRef" @click="onClick">
			<font-awesome-icon v-if="locked" icon="fa-solid fa-lock" />
			<font-awesome-icon v-else icon="fa-solid fa-lock-open" />
		</button>
	</div>
	<hint v-model="colorsStore.hintCopy">Copied</hint>
</template>
<script>
import { ref } from 'vue'
import Hint from '../hint/hint.vue'
import { useColorsStore } from '@/router/colorsStore'
export default {
	name:'color-item',
	components:{Hint},
	setup(){
		const locked = ref(false)
		const btnRef = ref()
		const colorsStore = useColorsStore()

		const copyToClickboard = (e) => {
			colorsStore.showHintCopy()
			return navigator.clipboard.writeText(e.srcElement.textContent)
		}

		const onClick = e => {
			locked.value = !locked.value
			btnRef.value.blur()
		}
		return {
			locked,
			copyToClickboard,
			onClick,
			btnRef,
			colorsStore
		}
	}
	
}
</script>

<style scoped lang="scss" src="./style.scss"/>
