<template>
	<teleport to="body">
		<Transition name="hint">
		<div v-if="modelValue" class="hint">
			<slot></slot>
		</div>
	</Transition>
	</teleport>


</template>

<script>

export default {
	name: 'hint',
	props:{
		modelValue:{
			type: Boolean,
			default: false,
			required: false
		}
	},
	emits:['update:modelValue'],
}
</script>



