import { defineStore } from "pinia"
import { ref } from "vue"

export const useColorsStore = defineStore("colors", () => {
  const hintSpace = ref(true)
  const hintCopy = ref(false)


	let timer
	
  const showHintCopy = () => {
    const hideHintCopy = () => (hintCopy.value = false)

    hintCopy.value = true

    clearTimeout(timer)

    timer = setTimeout(hideHintCopy, 500)
  }

  return {
    hintSpace,
    hintCopy,
    showHintCopy,
  }
})
