<template>
	<color-list :count="5"/>
		<hint v-model="colorsStore.hintSpace">Space</hint>
</template>

<script>
import { ref, onMounted } from 'vue';
import ColorList from './components/color-list/color-list.vue'
import Hint from './components/hint/hint.vue';
import { useColorsStore } from './router/colorsStore';

export default {
  name: 'App',
  components: {
		ColorList,
		Hint
  },
	setup(){
		const colorsStore = useColorsStore()

		onMounted(() => {
			document.addEventListener('keypress', (e) => {
				e.code === 'Space' ? colorsStore.hintSpace = false : null
			})
		})
		return {
			colorsStore
		}
	}
}
</script>

<style lang="scss" src="./assets/style/index.scss"/>
