<template lang="">
	<div class="color-list">
		<color-item v-for="(item,i) in items"/>
	</div>
</template>
<script>
import ColorItem from '../color-item/color-item.vue'
import { ref, computed, onMounted } from 'vue'
import chroma from "chroma-js";


export default {
	name: 'color-list',
	components: { ColorItem },
	props:{
		count: {
			type: Number,
			default: 5,
			required: false
		}
	},
	setup({count}) {
		const items = ref(Array(count))
		let cols
		let startHash = document.location.hash

		const generateRandomColor = () => {
			const hexCodes = '0123456789ABCDEF'
			let color = ''

			for(let i = 0; i < 6; i++){
				color += hexCodes[Math.floor(Math.random() * hexCodes.length)]
			}
			return '#' + color
		}

		const setRandomColors = (isInitial) => {
			const colors = isInitial ? getColorsFromHash() : []
			cols.forEach((col,i) => {
				const text = col.querySelector('h2')
				const button = col.querySelector('button')
				const color = isInitial ? colors[i] : chroma.random()

				if (col.classList.contains('color-item--locked')) {
					colors.push(text.textContent)
					return
				}

				if(!isInitial){
					colors.push(color)
				}
				col.style.background = color
				text.textContent = color
				setTextColor(text, button, color)
			})
			updateColorsHash(colors)
		}

		const setTextColor = (text, button, color) => {
			const luminance = chroma(color).luminance()
			if(luminance > 0.5){
				text.style.color = '#000'
				button.style.color = '#000'
			}
			else{
				text.style.color = '#fff'
				button.style.color = '#fff'
			}
		}

		document.addEventListener('keydown', (e)=> {
			e.code === 'Space' ? setRandomColors() : null
		})

		const updateColorsHash = (colors = []) => {
			document.location.hash = colors.map(color => color.toString().substring(1)).join('-')
		}

		const getColorsFromHash = () => {
			return document.location.hash.substring(1).split('-').map(color => '#' + color)

		}


		onMounted(()=>{
			cols = document.querySelectorAll('.color-item')
			startHash.length ? setRandomColors(true) : setRandomColors()
			
		})


		return {
			items,
			setRandomColors
		}
	}
}
</script>


<style scoped lang="scss" src="./style.scss"/>
	